/*---
 section: Utilities
 title: Positioning
 ---

 * `u-attach--top`
 * `u-attach--top-right`
 * `u-attach--top-left`
 * `u-attach--bottom`
 * `u-attach--bottom-right`
 * `u-attach--bottom-left`
 * `u-attach--middle`

 */

%sticky-top {
  @mixin plank;
  top: 0;
}

.u-attach--top {
  @extend %sticky-top;
}

.u-attach--top-right {
  position: fixed !important;
  right: 0;
  top: 0;
}

.u-attach--top-left {
  position: fixed !important;
  left: 0;
  top: 0;
}

.u-attach--bottom {
  @mixin plank;
  bottom: 0;
}

.u-attach--bottom-right {
  position: fixed !important;
  bottom: 0;
  right: 0;
}

.u-attach--bottom-left {
  position: fixed !important;
  bottom: 0;
  left: 0;
}

.u-attach--middle {
  position: fixed !important;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

