/* Lists */

%no-bullet {
  list-style-image: none;
  list-style-type: none;
  margin-left: 0;
}

%no-bullets {
  list-style: none;
  li { @extend %no-bullet; }
}

%list-horizontal {
  @extend %reset-box;
  clear: both;
}

.u-list-inline,
%list-inline {
  @extend %no-bullets;
  margin-top: 0;
  margin-bottom: .5em;
  margin-left: .5em;
  overflow: hidden;
  padding: 0;

  > li {
    display: block;
    float: left;
    margin-left: 1em;

    > * { display: block; }
  }
}

