@define-mixin color-class $color {
  background: $color !important;
  color: contrast($color);
}

.u-blue-50 { @mixin color-class var(--blue-50); }
.u-blue-100 { @mixin color-class var(--blue-100); }
.u-blue-200 { @mixin color-class var(--blue-200); }
