.u-blue-600 { @mixin color-class var(--blue-600); }
.u-blue-700 { @mixin color-class var(--blue-700); }
.u-blue-800 { @mixin color-class var(--blue-800); }
.u-blue-900 { @mixin color-class var(--blue-900); }
