/*---
 section: Containers
 title: Modal
 ---

 ```example:html

 <div class="c-modal is-active">
   <div class="c-modal__card [u-attach--middle]">
     <div class="c-modal__title">
       <span>Modal Title</span>
       <button class="o-close">
         <i class="material-icons">close</i>
       </button>
     </div>
     <div class="c-modal__content u-row u-row--colapse">
       <p>This is a card in a modal, what will they think of next?</p>
       <div class="c-modal__actions">
         <button class="c-button--anchor">Close</button>
       </div>
     </div>
   </div>
   <div class="c-modal__close"></div>
 </div>
 ```
 */

/* @import 'stylep-modal'; */

@define-mixin modal $titlebar: var(--blue-800), $background: var(--grey-100) {
  @extend %start;
  @extend %row;
  @extend %fade-out;
  z-index: 500;

  &:target,
  &.is-active {
    @extend %fade-in;
  }

  &__close {
    @extend %fade-out;
    background: rgba(0, 0, 0, .6);
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 400;

    .is-active & { @extend %fade-in; }
  }

  &__card {
    @mixin card $background;
    min-height: 120px;
    min-width: 120px;
    z-index: 450;
  }

  &__title {
    /* @extend %row-break; */
    background-color: $titlebar;
    color: var(--white);
    font-size: 1.3em;
    height: 55px;
    margin-bottom: .5em;
    margin-top: -.5em;
    padding: .5em .5em;
    position: relative;

    .o-close {
      cursor: pointer;
      position: absolute;
      right: 4px;
      top: 9px;

      i {
        color: rgba(255, 255, 255, .5);
        font-size: 40px;
      }
    }
  }

  &__content {
    padding: .5em 1em;
  }

  &__actions {
    @extend %end;
  }
}

.c-modal {
  @mixin modal;

  &--help {
    @mixin modal var(--blue-700), var(--white);

    &__title {
      @extend %row;
      @extend %middle;
      border-radius: 4px 4px 0 0;

      i:first-child {
        margin-right: 5px;
      }

      .o-close i {
        font-size: 30px !important;
        top: .4rem;
      }

      h6 {
        display: inline-block;
        margin: 5px 0 !important;
      }
    }

    &__card {
      border-radius: 4px;
    }
  }
}

