/* stylelint-disable */

@import "stylep-card";

:root {
  --bar-bg: var(--grey-800);
}

.t-docs {

  .c-bar--top {
    background-color: var(--bar-bg);
  }

  .o-brand {
    background-image: url('dist/img/navis-ui-logo.svg');
  }

  .section {
    border-bottom: 1px solid var(--grey-300);
    padding-bottom: 2em;
  }

  .c-menu {
    @mixin menu var(--bar-bg), var(--orange-500);

    > ul > li:hover > a + ul {
      position: absolute;
      text-align: left;
    }
  }


  .example {
    border: var(--grey-500) 1px solid;
    padding: 1em;
    position: relative;

    > * {
      position: relative !important;
    }
  }

  iframe {
    width: 100% !important;
  }

  pre,
  code { background-color: rgba(0,0,0,.04); }

  pre code { background-color: transparent; }

  .token.punctuation { color: var(--grey-800); }

  .token.tag { color: var(--red-600); }

  .token.attr-name { color: var(--orange-600); }

  .token.attr-value { color: var(--green-700); }

  .token.function { color: var(--blue-600); }

  .token.string { color: var(--green-700); }

  .token.comment { color: var(--grey-700); }


  .u-chip {
    font-family: monospace;
    height: 50px;
    padding: .5em 2em;
  }

  .u-chip-large {
    height: 100px;
    font-size: 2rem;
    font-family: inherit;
    padding: 1em;
    margin-top: .5em;
  }

  .box-row { margin-bottom: 1rem; }

  .box,
  .box-first,
  .box--large,
  .box-nested,
  .box-row {
    background-color: var(--blue-600);
    color: var(--white);
    min-height: 2rem;
    overflow: hidden;
    padding: .75rem;
    position: relative;
    text-align: center;
    font-size: .8rem;
    i {
      position: relative;
      top: 3px;
    }
  }

  .box-first {
    background-color: var(--blue-700);
  }

  .box-nested {
    background-color: var(--blue-800);
  }

  .box--large {
    height: 5rem;
  }

  .icon-example {
    li {
      display: inline-block;
      list-style: none;
      overflow: hidden;
      margin: 0 35px 10px 0;
      padding: 0;
      width: 25%;
    }
    i {
      font-size: 26px;
      float: left;
    }
    code {
      width: 250px;
      display: block;
      float: right;
    }
  }
}

.t-devcards {
  overflow-x: hidden;

  #com-rigsomelight-devcards-main {
    @extend %column-12;

    @media (--md) {
      @extend %column-10;
      margin: 0 auto;
    }

    @media (--lg-up) {
      @extend %column-6;
      margin: 0 auto;
    }
  }

  .com-rigsomelight-devcards {
    &-card-base {
      padding: 20px 0;
    }

    &-list-group {

      &-item {
        display: block;
        margin: 0 30px -1px;
        padding: 10px 14px;
        &:not(:last-child) {
          border-bottom: 1px solid var(--grey-100);
        }
      }
    }

    &-badge {
      background-color: var(--grey-500);
      border-radius: 10px;
      color: #fff;
      display: inline-block;
      font-size: 12px;
      font-weight: 100;
      line-height: 1;
      min-width: 10px;
      padding: 3px 7px;
      text-align: center;
      vertical-align: baseline;
      white-space: nowrap;
    }

    &-typog a {
      color: var(--blue-700);
    }

    &-card-base,
    &-card-base-no-pad {
      @mixin card-inline;
      @mixin card-solid;
      display: block;
    }

    &-devcard-padding {
      padding: .5em 1em 1em;
    }

    &_rendered-card {
      h1, h2, h3, h4, h5, h6 {
        margin-top: 0;
      }
      h2 {
        font-size: 24px;
      }
    }

    &-card-hide-border,
    &-breadcrumbs {
      background-color: transparent !important;
      box-shadow: none !important;
    }

    &-panel-heading {
      background-color: var(--blue-50);
      border-bottom: 1px solid var(--blue-100);
      padding: .5em 1em;
      a {
        font-size: 24px;
      }
      .com-rigsomelight-devcards-badge {
        margin-top: 9px !important;
      }
    }

    &_set-current-path {
      position: relative;
      bottom: 4px;
      margin: 0 3px;
    }

    &-breadcrumb-sep {
      font-size: 25px;
    }
    &-markdown {
    }

    &-history-control-bar {
      background-color: rgb(255, 252, 234);
      padding-top: 5px;
      padding-bottom: 3px;
      margin: 14px -30px;
      padding-left: 14px;
      padding-right: 14px;
      text-align: center;
    }

    &-history-control-left {
      display: inline-block;
      height: 0;
      width: 0;
      border: 8px solid transparent;
      border-right-width: 16px;
      border-right-color: var(--white);
      margin-left: -10px;
    }

    &-history-control-right {
      display: inline-block;
      height: 0;
      width: 0;
      border: 8px solid transparent;
      border-left-width: 16px;
      border-left-color: var(--white);
      margin-right: -10px;
    }

    &-history-control-small-arrow {
      display: inline-block;
      height: 0;
      width: 0;
      border: 8px solid transparent;
      border-left-width: 9px;
      border-left-color: var(--white);
      margin-right: -10px;
    }

    &-history-stop {
      display: inline-block;
      height: 17px;
      width: 17px;
      background-color: var(--red-600);
      border-radius: 3px;
    }

    &-error {
      border-left: 5px solid var(--red-600);
      padding-left: 5px;
    }

  }

  button:not([class]),
  input[type="button"]:not([class]) {
    @extend .c-button;
  }

  .red-box {
    border: 5px solid var(--red-600);
  }

  /* Custom Classes */
  .selections {
    margin-left: 1.5em;
  }

}

/* stylelint-enable */

