/*---
 section: Forms
 title: Buttons
 ---

 Use these button classes on `<button>` or `<input type="submit">` element. It's easy to make a new button.

 ```example:html
 <button class="c-button">Default</button>
```

 #### Size and Form

 You can optionally use modifier classes that let you manipulate the size and shape of your button.

 ```example:html
 <button class="c-button">Default</button>
 <button class="c-button c-button--round">Round</button>
 <button class="c-button c-button--small">Small</button>
 <button class="c-button c-button--large">Large</button>
 <button class="c-button c-button--wide">Wide</button>
 ```

 #### Color

 Stateful color classes are provided to further communicate the intentions of your button action.

 ```example:html
 <button class="c-button"><i class="material-icons">fingerprint</i> Default</button>
 <button class="c-button--secondary"><i class="material-icons">warning</i> Secondary</button>
 <button class="c-button--alert"><i class="material-icons">add_alert</i> Alert</button>
 <button class="c-button--info"><i class="material-icons">info_outline</i> Info</button>
 <button class="c-button--link"><i class="material-icons">link</i> Link</button>
 <button class="c-button--anchor"><i class="material-icons">directions_boat</i> Anchor</button>
 ```

 #### States

 ```example:html
 <button class="c-button">Default</button>
 <button class="c-button" aria-hidden="true" disabled>Disabled</button>
 <button class="c-button" title="Click me to see the active state.">Active</button>
 ```

 #### Customize

 ```css
  --button-background: var(--blue-800);
  --button-background-hover: var(--blue-700);
  --button-color: contrast(var(--button-background));
  --button-disabled-opacity: .35;
  --button-font-size: 1.125rem;
  --button-font-size-sm: calc(var(--button-font-size) * .75);
  --button-font-size-lg: calc(var(--button-font-size) * 1.25);
  --button-round: var(--global-rounded);
 ```

 #### Horizontal Button Group

 ```example:html
 <div class="c-button-group">
   <button class="c-button">First</button>
   <button class="c-button">Second</button>
   <button class="c-button">Third</button>
 </div>
 ```

 #### Vertical Button Group

 ```example:html
 <div class="c-button-group--vertical">
   <button class="c-button">First</button>
   <button class="c-button">Second</button>
   <button class="c-button">Third</button>
 </div>
 ```
*/

@import 'stylep-button';

:root {
  --button-background: var(--blue-800);
  --button-background-hover: var(--blue-700);
  --button-color: contrast(var(--button-background));
  --button-disabled-opacity: .35;
  --button-font-size: 1.125rem;
  --button-font-size-sm: calc(var(--button-font-size) * .75);
  --button-font-size-lg: calc(var(--button-font-size) * 1.25);
  --button-round: var(--global-rounded);
}


/* Extended Button Pattern */

%button {
  @mixin button-inline 12.8px 24px, 0 2% 2% 0;
  transition: transform var(--global-duration) ease-out, background var(--global-duration) ease-in-out, box-shadow var(--global-duration) ease-in-out !important;

  /* Reset Firefox Box Model for Click Events */
  &::-moz-focus-inner {
    padding: 0 !important;
    border: 0 !important;
  }

  /* Element States */
  &:active { transform: scale(.95) perspective(1px); }
  &:focus { outline: 0; }

  /* Accessibilty Validation */
  &:not(button, [type=submit]) { @extend %assert-selector; }

  /* Icon */
  i { color: contrast(var(--button-background)); }
}

/* Classes */

/* Patterns First */
.c-button--wide {
  @mixin button-block 12.8px 24px, 0 2% 2% 0;

  @media (--sm) {
    &\@sm {
      @mixin button-block 12.8px 24px, 0 2% 2% 0;
    }
  }
}

.c-button {
  @extend %button;
  @mixin button-solid var(--button-background), var(--button-background-hover), #fff, 0 0 6px var(--shadow-color), var(--button-round);

  &--success {
    @mixin button-solid var(--green-700), var(--green-600), var(--white), 0 0 6px var(--shadow-color), var(--button-round);
    @extend %button;
  }

  &--secondary {
    @mixin button-solid var(--orange-700), var(--orange-600), var(--white), 0 0 6px var(--shadow-color), var(--button-round);
    @extend %button;
  }

  &--alert {
    @mixin button-solid var(--red-700), var(--red-600), var(--white), 0 0 6px var(--shadow-color), var(--button-round);
    @extend %button;
  }

  &--info {
    @mixin button-solid var(--blue-400), var(--blue-300), var(--white), 0 0 6px var(--shadow-color), var(--button-round);
    @extend %button;
  }

  &--anchor {
    @mixin button-solid transparent, transparent, var(--blue-700), none, var(--button-round);
    font-size: 12px;
    padding-left: var(--gutter-width);
    padding-right: var(--gutter-width);
    text-transform: uppercase;

    &:hover,
    &:focus { text-decoration: underline !important; }

    i {
      bottom: 1.5px;
      color: var(--blue-700);
      font-size: 20px;
      position: relative;
    }

    @extend %button;
  }

  &--link {
    @mixin button-solid var(--background-color), var(--white), var(--black), 0 0 3px var(--grey-300), var(--button-round);
    border: 1px solid transparent;
    padding: .7em !important;
    position: relative;

    &:hover,
    &:focus { border: 1px solid var(--grey-300); }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 { margin-top: 0; }

    @extend %button;

    i {
      color: contrast(var(--background-color));
    }
  }

  &--round { 
    &,
    &::after {
      border-radius: 50px !important;
    }
  }

  &--small {
    font-size: var(--button-font-size-sm) !important;
    padding: .7em;
  }

  &--large { font-size: var(--button-font-size-lg) !important; }

  /* Gecko does not support flexbox buttons */


  &--postfix {
    border-radius: 0 var(--global-rounded) var(--global-rounded) 0;
    height: 38px;
    margin-left: -3px;
    padding: .48em;

    i { font-size: 22px !important; }
  }

  &--postinset {
    background-color: transparent;
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
  }
}

.t-default {
  & button,
  & [type="submit"] {
    &[disabled] {
      opacity: var(--button-disabled-opacity);
      -ms-pointer-events: none;
      pointer-events: none;

      &:not([aria-hidden]) { @extend %assert-selector; }
    }
    &.is-active {
      box-shadow: inset 0 1px 35px var(--black);
    }
  }
}


/* Groups */

%button-group,
.c-button-group {
  @mixin button-group-inline;
  @mixin button-group-pill;
}

%button-group-vertical,
.c-button-group--vertical {
  clear: both;
  list-style: none;

  [class*='c-button'] {
    margin: 0 1px 2px 0 !important;
    width: 100%;
  }
}

.o-button-group-label {
  margin-right: rs(.5);
}

.c-button-group--toggle {
  @extend %button-group;
  @extend %text-middle;
  border: 1px solid var(--grey-600) !important;
  border-radius: var(--global-rounded);
  display: inline-block;
  min-height: 38px;

  .c-bar--tools & { margin-bottom: 0 !important; }

  & button,
  & [type="submit"] { margin: 1px !important; }
}

.o-action-area {
  @extend %row;
  @extend %end;
  @extend %between;

  button:last-child {
    margin-right: rs(.25) !important;
  }
}

