/*---
 section: Navigation
 title: Bar
 ---

Use the class `.c-bar` on a `<div>` or `<header>` to get an application style top bar.

```example:html
<header class="c-bar">
  <nav>
    <ul>
      <li><h1>Title</h1></li>
      <li><i class="material-icons">play_arrow</i> <h2>Item</h2></li>
    </ul>
  </nav>
  <nav>
    <ul>
      <li><a href="#"><i class="material-icons">help</i></a></li>
    </ul>
  </nav>
</header>
```

```example
<div class="u-row">
 <div class="u-column--6@md">
   <header class="u-orange-600 c-bar">
      <nav>
        <ul><li><a href="#"><i class="material-icons">help</i></a></li></ul>
      </nav>
      <nav>
        <ul>
          <li><h2>Reverse Bar</h2></li>
        </ul>
      </nav>
   </header>
   </div>
   <div class="u-column--6@md">
   <header class="c-bar u-red-600">
      <nav>
        <ul><li><h2>Half Bar</h2></li></ul>
      </nav>
      <nav>
        <ul><li><a href="#"><i class="material-icons">star</i></a></li></ul>
      </nav>
   </header>
 </div>
</div>
```

 */

@define-mixin bar $background, $border-color: transparent, $border-width: 0 0 2px {
  @extend %row;
  background-color: $background;
  border-width: $border-width;
  border-style: solid;
  border-color: $border-color;
  color: contrast($background);
  max-height: 58px;
  min-height: 58px;
  padding: 0 rs(.5);
  z-index: 100;

  > a:not([class]),
  i {
    color: contrast($background);
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    height: 46px;
  }

  > :nth-child(even) > ul {
    @extend %end;

    > li {
      margin-left: 10px;
    }
  }

  > :nth-child(odd) > ul {
    @extend %start;

    > li {
      margin-right: 10px;
    }
  }

  nav {
    @extend %column;
    padding: .5em 0;

    > ul {
      @extend %middle;
      display: flex;
      flex-wrap: wrap;
      height: 41px;
      list-style: none;

      > li { display: inline-block; }

      h2,
      h3,
      h4,
      h5,
      h6 {
        display: inline;
        font-size: 1.25em;
        font-weight: 400;
        margin-right: 0;
        margin-top: 0;
        overflow: hidden;
        position: relative;
        text-transform: uppercase;
        padding: 0;
        vertical-align: middle;
      }
    }
  }

  i { font-size: 25px; }
}

%bar-base {
  @mixin bar var(--primary-color), transparent;
}

.c-bar {
  @extend %bar-base;

  &--top {
    @extend %sticky-top;
    @extend %bar-base;
  }

  &--bottom {
    @mixin bar var(--background-color), var(--grey-300), 2px 0 0;
    padding-top: rs(.5);
  }

  &--tools {
    @mixin bar var(--white);
    @extend %middle;
    box-shadow: 0 0 3px rgba(0, 0, 0, .36);
    margin-bottom: rs(.5);

    input {
      margin-bottom: 0 !important;
      padding-bottom: 2px !important;
      padding-top: 2px !important;
    }

    button {
      margin-bottom: 0 !important;
    }
  }
}

.o-brand {
  background-image: url("img/navis-logo--inverted.svg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 0;
  margin-top: rs(.25) !important;
  position: relative;
  text-indent: -99999em;
  width: 90px;
}

