/*---
 section: Base
 title: Layout
 ---

 The main layout depends on the `.s-app` class to render a holy grail layout that scales down nicely to mobile.

 <div class="u-column--6@md">
 <img src="dist/img/layout-holy-grail.svg" alt="">
 </div>

 [Mozilla - Holy Grail Layout](https://developer.mozilla.org/en-US/docs/Web/CSS/CSS_Flexible_Box_Layout/Using_CSS_flexible_boxes#Holy_Grail_Layout_example)
 */

.s-app {

  body {
    @extend %layout;
    border: 0;
    margin-top: 58px; /* combined height of .o-topbar */
    padding: 0;
  }

  &,
  body,
  #app,
  .s-app-container,
  .s-app-container > .u-row,
  ._aside {
    height: 100%;
  }
}

/** @define t-default */

.t-default {
  background-color: var(--background-color);
  box-sizing: border-box;
  color: var(--text-color);
  margin: 0;
  overflow-x: hidden;
  width: 100%;
}

/** @define o-main */

%main {
  @extend %container-fluid;
  @extend %row;
  @extend %row-break;
  height: calc(100% - 58px);
  min-height: 500px;
  /*overflow-x: hidden;*/

  @media (--sm) {
    flex-direction: column;
  }
}

.o-main {
  @extend %main;

  > aside {
    @extend %reset-mobile-order;
    flex: 1 6 20%;
    order: 1;

    @media (--sm) {
      min-height: 50px;
      max-height: 50px;
    }
  }

  > .o-article {
    @extend %reset-mobile-order;
    flex: 2 1 80%;
    order: 2;
  }

}

.o-megaheader {
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 200;
}

