/* stylelint-disable rule-properties-order */

:root {
  --gutter-width: var(--global-gutter-width);
  --outter-margin: var(--global-outter-margin);
  --gutter-compensation: calc((var(--gutter-width) * .5) * -1);
  --half-gutter-width: calc((var(--gutter-width) * .5));
  --sm-min: var(--global-small-media-min);
  --md-min: var(--global-medium-media-min);
  --lg-min: var(--global-large-media-min);
  --container-sm: calc(var(--sm-min) + var(--gutter-width));
  --container-md: calc(var(--md-min) + var(--gutter-width));
  --container-lg: calc(var(--lg-min) + var(--gutter-width));
  --col: calc(100% / 12);
  --col-1: var(--col);
  --col-2: calc(var(--col) * 2);
  --col-3: calc(var(--col) * 3);
  --col-4: calc(var(--col) * 4);
  --col-5: calc(var(--col) * 5);
  --col-6: calc(var(--col) * 6);
  --col-7: calc(var(--col) * 7);
  --col-8: calc(var(--col) * 8);
  --col-9: calc(var(--col) * 9);
  --col-10: calc(var(--col) * 10);
  --col-11: calc(var(--col) * 11);
  --col-12: calc(var(--col) * 12);
}
/* stylelint-enable */

%block { display: block; }


.u-reset-box,
%reset-box {
  border: 0 !important;
  margin: 0 !important;
  padding: 0 !important;
}

%reset-mobile-order {
  @media (--sm) {
    order: 0 !important;
  }
}

%spacer { flex: auto; }

/* Layout */

.u-layout,
%layout {
  display: flex;
  flex-direction: column;
}

/* Containers */

.u-container,
%container {
  margin-left: auto;
  margin-right: auto;
}

.u-container--flex,
%container-flex {
  @extend %reset-box;
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
}

.u-container--fluid,
%container-fluid {
  @extend %container;
  padding-left: var(--outter-margin, 2rem);
  padding-right: var(--outter-margin, 2rem);
}

/* Row */

.u-row,
%row {
  box-sizing: border-box;
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: var(--gutter-compensation, -.5rem);
  margin-right: var(--gutter-compensation, -.5rem);
}

.u-row--reverse,
%row-reverse { flex-direction: row-reverse; }

.u-first,
.u-column--first,
%first { order: -1; }

.u-last,
.u-column--last,
%last { order: 1; }

.u-row--break,
%row-break {
  margin-left: calc(-1rem - 1px);
  margin-right: calc(-1rem - 1px);
}

.u-row--collapse > [class*='u-column'] {
  padding-left: 0;
  padding-right: 0;
}


/* Columns */

%column-base {
  box-sizing: border-box;
  flex: 1 0 0%;
  max-width: 100%;
  padding-left: var(--half-gutter-width, .5rem);
  padding-right: var(--half-gutter-width, .5rem);
}

@define-mixin column $width {
  @extend %column-base;
  flex-basis: $width !important; /* Important for IE11 */
  max-width: $width;
}

.u-column,
%column {
  @extend %column-base;
}

.u-column--reverse, %column-reverse {
  flex-direction: column-reverse;
}

.u-column--1, %column-1 { @mixin column var(--col-1); }
.u-column--2, %column-2 { @mixin column var(--col-2); }
.u-column--3, %column-3 { @mixin column var(--col-3); }
.u-column--4, %column-4 { @mixin column var(--col-4); }
.u-column--5, %column-5 { @mixin column var(--col-5); }
.u-column--6, %column-6 { @mixin column var(--col-6); }
.u-column--7, %column-7 { @mixin column var(--col-7); }
.u-column--8, %column-8 { @mixin column var(--col-8); }
.u-column--9, %column-9 { @mixin column var(--col-9); }
.u-column--10, %column-10 { @mixin column var(--col-10); }
.u-column--11, %column-11 { @mixin column var(--col-11); }
.u-column--12, %column-12 { @mixin column var(--col-12); }

.u-push--1, %push-1 { margin-left: var(--col-1); }
.u-push--2, %push-2 { margin-left: var(--col-2); }
.u-push--3, %push-3 { margin-left: var(--col-3); }
.u-push--4, %push-4 { margin-left: var(--col-4); }
.u-push--5, %push-5 { margin-left: var(--col-5); }
.u-push--6, %push-6 { margin-left: var(--col-6); }
.u-push--7, %push-7 { margin-left: var(--col-7); }
.u-push--8, %push-8 { margin-left: var(--col-8); }
.u-push--9, %push-9 { margin-left: var(--col-9); }
.u-push--10, %push-10 { margin-left: var(--col-10); }
.u-push--11, %push-11 { margin-left: var(--col-11); }

.u-pull--1, %pull-1 { margin-right: var(--col-1); }
.u-pull--2, %pull-2 { margin-right: var(--col-2); }
.u-pull--3, %pull-3 { margin-right: var(--col-3); }
.u-pull--4, %pull-4 { margin-right: var(--col-4); }
.u-pull--5, %pull-5 { margin-right: var(--col-5); }
.u-pull--6, %pull-6 { margin-right: var(--col-6); }
.u-pull--7, %pull-7 { margin-right: var(--col-7); }
.u-pull--8, %pull-8 { margin-right: var(--col-8); }
.u-pull--9, %pull-9 { margin-right: var(--col-9); }
.u-pull--10, %pull-10 { margin-right: var(--col-10); }
.u-pull--11, %pull-11 { margin-right: var(--col-11); }

/* Grid Utilities */

.u-start,
%start {
  justify-content: flex-start;
  text-align: start;
}

.u-center,
%center {
  justify-content: center;
  text-align: center;
}

.u-end,
%end {
  justify-content: flex-end;
  text-align: end;
}

@define-mixin text-align $x, $y {
  align-items: $y;
  display: flex;
  justify-content: $x;
}

.u-text-center,
%text-center { @mixin text-align center, center; }

.u-text-middle,
%text-middle { @mixin text-align flex-start, center; }

.u-top,
%top { align-items: flex-start; }

.u-middle,
%middle { align-items: center; }

.u-bottom,
%bottom { align-items: flex-end; }

.u-around,
%around { justify-content: space-around; }

.u-between,
%between { justify-content: space-between; }

