/*---
 section: Containers
 title: Card
 ---

 ```example:html
 <div class="c-card">
   <h3>Card Title</h3>
   <p>I could have used lorem ipsum, but what's the fun in that?</p>
 </div>
 ```

 ### Customize

 ``` css
  --card-background-color: var(--white);
  --card-shadow: 0 0 10px rgba(0, 0, 0, .4);
  ```
 */

@import "stylep-card";

:root {
  --card-background-color: var(--white);
  --card-shadow: 0 0 10px rgba(0, 0, 0, .4);
}

@define-mixin card $background: var(--card-background-color), $shadow: var(--card-shadow) {
  @mixin card-block;
  @mixin card-solid $background, #444, $shadow {
    padding: .5rem 1rem;

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin-top: 10px;
    }
  }
}

%card-list {
  @mixin card-block;
  @mixin card-solid var(--card-background-color), #444, 0 1px 4px rgba(0, 0, 0, .4) {
    margin-bottom: 0;
  }
}

.c-card {
  @mixin card;

  &--transparent {
    @mixin card transparent;
    box-shadow: none !important;
  }
}
